import React, { useState, useEffect } from "react";
import playerData from "../Data/playerInfo.json";
import WikiTable from "./WikiTable"; 
import '../styles/GuessLogicStyle.css';

const GuessLogic = () => {
  const [shuffledPlayerIds, setShuffledPlayerIds] = useState([]); // Store shuffled IDs
  const [currentPlayerIndex, setCurrentPlayerIndex] = useState(0); // Index of the current player
  const [selectedPlayer, setSelectedPlayer] = useState(null); // Store the currently displayed player
  const [userGuess, setUserGuess] = useState(""); // Track the user's guess
  const [guessResult, setGuessResult] = useState(null); // Store the result of the guess (correct/incorrect)
  const [points, setPoints] = useState(0); // Store the current points
  const [formSubmitted, setFormSubmitted] = useState(false); // Track if the form was submitted
  const [gameStarted, setGameStarted] = useState(false); // Track if the game has started
  const [isWrongGuess, setIsWrongGuess] = useState(false);


  
  // Step 1: Function to shuffle an array (Fisher-Yates algorithm)
  const shuffleArray = (array) => {
    for (let i = array.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [array[i], array[j]] = [array[j], array[i]]; // Swap elements
    }
    return array;
  };

  // Step 2: Start the game by shuffling the player IDs and selecting the first player
  const startGame = () => {
    const playerIds = playerData.map((player) => player.id); // Extract player IDs from the data
    const shuffledIds = shuffleArray([...playerIds]); // Shuffle the player IDs
    setShuffledPlayerIds(shuffledIds); // Set the shuffled IDs
    setCurrentPlayerIndex(0); // Start at the first player
    setPoints(0); // Reset points at the start
    setGameStarted(true); // Set the game state to started
  };

  // Step 3: Whenever the currentPlayerIndex changes, update the selectedPlayer
  useEffect(() => {
    if (shuffledPlayerIds.length > 0) {
      const playerIdToDisplay = shuffledPlayerIds[currentPlayerIndex];
      const player = playerData.find((player) => player.id === playerIdToDisplay);
      setSelectedPlayer(player); // Set the player based on the current ID
    }
  }, [currentPlayerIndex, shuffledPlayerIds]);

  // Levensthein distance function. Used to calculate the difference between the inputted name vs right answer. 
  const levenshteinDistance = (a, b) => {
    const matrix = [];
  
    // Initialize the matrix
    for (let i = 0; i <= b.length; i++) {
      matrix[i] = [i];
    }
    for (let j = 0; j <= a.length; j++) {
      matrix[0][j] = j;
    }
  
    // Fill the matrix
    for (let i = 1; i <= b.length; i++) {
      for (let j = 1; j <= a.length; j++) {
        if (b[i - 1] === a[j - 1]) {
          matrix[i][j] = matrix[i - 1][j - 1];
        } else {
          matrix[i][j] = Math.min(
            matrix[i - 1][j - 1] + 1, // substitution
            matrix[i][j - 1] + 1, // insertion
            matrix[i - 1][j] + 1 // deletion
          );
        }
      }
    }
  
    return matrix[b.length][a.length];
  };
  

  // Step 4: Handle the submission of the guess
  const handleGuessSubmit = (event) => {
    event.preventDefault();
    setFormSubmitted(true);
  
    // Normalize the user's guess and the player's name for comparison
    const normalizeString = (str) => {
      return str.normalize("NFD").replace(/[\u0300-\u036f]/g, "").toLowerCase();
    };
  
    if (selectedPlayer) {
      const userGuessNormalized = normalizeString(userGuess);
      const playerNameNormalized = normalizeString(selectedPlayer.playerName);
  
      const isCorrectGuess = userGuessNormalized === playerNameNormalized;
  
      if (isCorrectGuess) {
        setGuessResult("correct"); // Set the result of the guess as correct
        setPoints((prevPoints) => prevPoints + 1); // Increment points if correct
        setTimeout(() => {
          // Move to the next player after a short delay
          if (currentPlayerIndex < shuffledPlayerIds.length - 1) {
            setCurrentPlayerIndex((prevIndex) => prevIndex + 1); // Move to the next player
          } else {
            alert("You've gone through all players! Starting over.");
            startOver(); // Restart if all players are shown
          }
          setUserGuess(""); // Clear the guess input
          setGuessResult(null); // Clear the guess result
          setFormSubmitted(false); // Reset form submission state
        }, 2000);
      } else {
        // Calculate the Levenshtein distance between the user's guess and the player's name
        const distance = levenshteinDistance(userGuessNormalized, playerNameNormalized);
        const threshold = 3; // Set your threshold for a "close" guess
  
        if (distance <= threshold) {
          // Handle close guess, give user another chance
          setGuessResult("close"); // Indicate a close guess
        } else {
          setGuessResult("incorrect"); // Indicate an incorrect guess
          setIsWrongGuess(true); // Trigger the shake animation
          setTimeout(() => setIsWrongGuess(false), 500); // Reset after animation
          setPoints(0); // Reset points if incorrect
        }
      }
    }
  };

  const [highScore, setHighScore] = useState(() => {
    // Get the high score from localStorage if it exists
    const savedHighScore = localStorage.getItem('highScore');
    return savedHighScore ? parseInt(savedHighScore, 10) : 0;
  });
  useEffect(() => {
    // Update localStorage if the high score changes
    if (points > highScore) {
      setHighScore(points);
      localStorage.setItem('highScore', points);
    }
  }, [points, highScore]);
  

  // Step 5: Restart the game
  const startOver = () => {
    startGame(); // Re-shuffle the players and reset the game
    setUserGuess(""); // Clear the guess input
    setGuessResult(null); // Clear the guess result
    setFormSubmitted(false); // Reset form submission state
  };

  return (
    <div className={`"guess-container" ${isWrongGuess ? 'wrong-guess' : ''}`}>
           
      {/* Start Game Button */}
      {!gameStarted ? (
        <button onClick={startGame} className="start-game-button">
          Start Game
        </button>
      ) : (
        <>
          <form onSubmit={handleGuessSubmit} className="guess-form">
            <input
              type="text"
              value={userGuess}
              onChange={(e) => setUserGuess(e.target.value)}
              disabled={guessResult === true}
              className="guess-input"
              placeholder="Enter the player's name"
            />
            <button type="submit" className="guess-button">Submit Guess</button>
          </form>

          <div className="guess-result">
          {guessResult === "correct" ? (
            <div>
              <h2 className="correct">{selectedPlayer.playerName} was correct!</h2>
              {/* <p>Loading new player...</p> */}
            </div>
          ) : guessResult === "incorrect" && selectedPlayer ? (
            <h2 className="incorrect">
              You guessed wrong! The player was {selectedPlayer.playerName}.
              <button onClick={startOver} className="randomize-button">Start Over</button>
            </h2>
          ) : guessResult === "close" ? (
            <h2 className="close">
              Close! Your guess was almost correct, try again!
            </h2>
          ) : null}
        </div>

          <div className="points-display">
            <h2>Current score: {points}</h2>
            <h3>High Score: {highScore}</h3>
          </div>

          {/* Player info table */}
          {selectedPlayer && <WikiTable playerId={selectedPlayer.id} />}

          
        </>
      )}
    </div>
  );
};

export default GuessLogic;
