import React, { useEffect, useState } from "react";
import playerData from '../Data/playerInfo.json';
import '../styles/WikiTableStyle.css'

const WikiTable = ({ playerId }) => {
    const [selectedPlayer, setSelectedPlayer] = useState(null);
    
    // Fetch player data based on the playerId passed as a prop
    useEffect(() => {
        const player = playerData.find((player) => player.id === playerId);
        setSelectedPlayer(player);
    }, [playerId]);
    
    if (!selectedPlayer) {
        return null;
    }

    return (
        <div className="player-container">
            {/* <h1 className="player-name">{selectedPlayer.playerName}</h1> */}
            <div className="table-container">
                <table className="player-table">
                    <thead>
                        <tr>
                            <th>Years</th>
                            <th>Clubs</th>
                        </tr>
                    </thead>
                    <tbody>
                        {selectedPlayer.years.map((year, index) => (
                            <tr key={index}>
                                <td>{year.Year}</td>
                                <td>{selectedPlayer.clubs[index]}</td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    );
};

export default WikiTable;
